.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-decoration: none;
}

.copyright__name {
  font-size: 16px;
  line-height: 1;
  font-family: $secondary-font;
  color: $color-font;
}

.copyright__icone {
  display: block;
  transition: all ease 0.3s;
  fill: #666666;
}

@media (min-width: $tablet-width) {
  .copyright {
    width: 162px;
    transition: all ease 0.1s;

    &:hover {
      .copyright__icone {
        fill: $color-accent;
      }
    }

    &:active {
      opacity: 0.3;
    }
  }
}
