.field-checkbox {
  margin-bottom: 13px;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.field-checkbox__input:checked + .field-checkbox__input-element .field-checkbox__icon {
  width: 21px;
  height: 16px;
}

.field-checkbox__input:checked + .field-checkbox__input-element,
.field-checkbox__input:checked + .field-checkbox__input-element + .field-checkbox__text {
  // opacity: 0.3;
}

.field-checkbox__label {
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all ease 0.1s;

  &:hover {
    .field-checkbox__input-element {
      border-color: #bababa;
    }
  }

  &:focus,
  &:active {
    border-color: #bababa;
    outline: 1px dotted #444444;
    outline-offset: 3px;
  }
}

.field-checkbox__input-element {
  position: relative;
  display: block;
  flex-shrink: 0;
  width: 30px;
  height: 30px;
  margin-right: 16px;
  border: 1px solid #cdcdcd;
}

.field-checkbox__text {
  font-size: 16px;
  line-height: 1;
  color: #222222;
  text-transform: uppercase;
}

.field-checkbox__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  transform: translate(-50%, -50%);
  transition: width 0.3s, height 0.3s;
  fill: $color-accent;
}

@media (min-width: $tablet-width) {
  .field-checkbox {
    width: 40.12%;
    margin-right: 60px;
    margin-bottom: 32px;

    &:nth-of-type(2n) {
      margin-right: 0;
    }

    &:nth-last-of-type(-n+2) {
      margin-bottom: 0;
    }
  }

  .field-checkbox__input-element {
    width: 40px;
    height: 40px;
  }

  .field-checkbox__text {
    font-size: 20px;
  }
}

@media (min-width: $desktop-width) {
  .field-checkbox {
    width: 245px;
    margin-right: 80px;

    &:nth-of-type(2n) {
      margin-right: 80px;
    }

    &:nth-of-type(4n) {
      margin-right: 0;
    }

    &:nth-last-of-type(-n+2) {
      margin-bottom: 32px;
    }
  }
}
