*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
html {
  min-width: 320px;
  height: 100%;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  font-family: "Oswald", "Arial", sans-serif;
  color: $color-font;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border-style: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
