.logo-page-header {
  .logo-page-header__image {
    height: 38px;
  }

  .logo-page-header__title {
    width: 101px;
    height: 18px;
    margin-right: calc((100% - 320px) / 2 + 77px);
    margin-left: auto;
  }
}

.logo-page-footer {
  display: block;

  .logo-page-footer__image {
    margin: 0 auto;
  }
}

@media (min-width: $tablet-width) {
  .logo-page-header {
    transition: opacity ease 0.3s;

    &:hover {
      opacity: 0.8;
    }

    &:active {
      opacity: 0.6;
    }

    .logo-page-header__image {
      width: 173px;
      height: 58px;
    }

    .logo-page-header__title {
      display: none;
    }
  }

  .logo-page-footer {
    transition: opacity ease 0.3s;

    &:hover {
      opacity: 0.6;
    }
  }
}

@media (min-width: $desktop-width) {
  .logo-page-header {
    .logo-page-header__image {
      width: 202px;
      height: 59px;
    }
  }
}
